.who-main {
  margin: auto;
  padding: 4rem;
}

.who-main h4 {
    font-style: italic;
    font-family: serif;
    font-size: 4rem;
    color: rgb(107, 193, 225);
    text-align: center;
    margin: auto;
    padding: 1rem;
   
  }

.who-main h5 {
    font-family: serif;
    font-size: 3rem;
    color: rgb(107, 193, 225);
    text-align: center;
    margin: auto;
    padding: 0rem;
}

.who-main p {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 1rem;
    color: rgb(122, 119, 119);
    text-align: center;
    margin: auto;
    padding: 1rem;
  }

.who-main .Logo-AD {
  height: 44.5vh;
  display: flex;
  justify-items: center;
  align-items: center;
  margin: auto;
  padding: 3rem;
}