.Sand {
    text-align: center;
    margin: 0;
    padding: 0;
    height: 100vh;
}

.Sand-logo {
  height: 80vmin;
  pointer-events: none;
  content: center;
  padding: 0rem;
}
  
@media screen and (prefers-reduced-motion: no-preference) {
    .Sand-logo {
    animation: Sand-logo-spin;
    animation-duration: 8s;
  }
}
  
@keyframes Sand-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
  
.Sand h1 {
  font-style: italic;
  font-family: serif;
  font-size: 3rem;
  font-style: italic;
  color: rgb(107, 193, 225);
  padding: 1rem;
}
  
.Sand h2 {
  font-style: italic;
  font-family: serif;
  font-size: 2rem;
  color: rgb(107, 193, 225);
  padding: 0rem;
}

.moments {
  font-size: 1rem;
  color: rgb(0, 102, 140);
  font-family: serif;
  text-align: center;
  margin: auto;
  padding: 0rem;
}