.Contacts {
    height: 100vh;
    flex-direction: column;
    justify-content: center;
    font-size: 1rem;
    color: rgb(101, 155, 202);
    text-align: center;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.contacts-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 1.5rem;
    width: 400px;
    margin: auto;
    box-shadow: 0px 0px 12px var(--border);
    border-radius: 8px;
    background-color: var(--background);
}
.contacts-container h1 {
    color: rgb(101, 155, 202);
    font-size: 3rem;
    
}

.form {
    width: 100%;
    display: flex;
    flex-direction: column;
   
}

.button {
    color: rgb(0, 0, 0);
   
}

@media screen and (min-width: 640px) {
.button {
    font-weight: 400;
}
}
