.buying-main h1 {
    text-align: center;
    margin: auto;
    display: flow-root;
    padding: 0rem;
    font-size: 4rem;
    color: rgb(83, 119, 170);
}

.buying-main h2 {
    font-size: 1.5rem;
    text-decoration: none;
    color: rgb(200, 182, 143);
    text-align: center;
    display: flow-root;
}

.buying-main p {
    font-size: 1.5rem;
    text-decoration: none;
    color: rgb(83, 119, 170);
    text-align: center;
    display: flow-root;
}

.buying-main ul {
    text-align: center;
    font-size: 1rem;
    display: flow-root;
    color: rgb(200, 182, 143);
}

.First-Jordans {
    height: 28vmin;
}

.Second-Jordans {
    height: 35vmin;
}

.Third-Jordans {
    height: 30vmin;
}

.First-Jordans:hover {
    transform: scale(1.3);
}

.Second-Jordans:hover {
    transform: scale(1.2);
}

.Third-Jordans:hover {
    transform: scale(1.5);
}