.app .theme-toggle {
    text-align: right;
    margin: auto;
    background-color: var(--background);
    color: var(--text-primary);
    display: flow-root;
    flex-direction: column;
    justify-content: right;
    font-size: 2rem;
    transition: all .7s;
    padding: 1rem;
  }

  :root {
    --background: #e9f6fe;
    --border: #333;
    --form-bg: #000000;
  }
  
  [data-theme='dark'] {
    --background: #000000;
    --border: #808080;
    --form-bg: #000000;
  }