
.header {
    position: absolute;
    width: 100%;
    height: 90px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 3rem;
    z-index: 10
}


.nav-menu {
    display: flex;
}

.nav-menu li {
    padding: 0 1rem;
}

.nav-menu a {
    font-size: 1rem;
    text-decoration: none;
    font-weight: 00;
}

.hamburger {
    display: none;
}

@media screen and (max-width:1130px) {
    .hamburger {
        display: block;
    }

    .nav-menu {
        position: absolute;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        top: 0;
        left: -100%;
        text-align: center;
        width: 100%;
        height: 100vh;
        transition: 0.5s;
        z-index: -1;
        background: rgba(107, 104, 104, 0.9);
    }

    .nav-menu.active {
        left: 0;
    }
    .nav-menu li {
        padding: 1rem 0;
        height: 100vh;
    }

    .nav-menu a {
        font-size: 2rem;
        height: 10vh;
    }
}